.newsletter {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 50%, #0d0d0d 100%);
  padding: 0 0 2rem 0;
  position: relative;
  overflow: hidden;
}

.newsletter::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 191, 255, 0.3), transparent);
}

.newsletter::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba(0,191,255,0.1)" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/></svg>');
  opacity: 0.3;
}

.newsletter-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 3rem 1.5rem 2rem 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  gap: 3rem;
}

.newsletter-text {
  text-align: center;
  max-width: 600px;
}

.newsletter-title {
  color: #00bfff;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  line-height: 1.2;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  max-height: calc(1.8rem * 1.2 * 2);
}

.newsletter-subtitle {
  color: #ff6b6b;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  font-family: 'Oswald', sans-serif;
}

.newsletter-description {
  color: rgba(0, 191, 255, 0.9);
  font-size: 0.9rem;
  line-height: 1.5;
  margin-bottom: 1.5rem;
}

.newsletter-form {
  display: flex;
  gap: 0.75rem;
  max-width: 400px;
  margin: 0 auto;
  flex-wrap: wrap;
}

.email-input {
  flex: 1;
  min-width: 200px;
  padding: 0.75rem 1rem;
  border: 2px solid rgba(0, 191, 255, 0.3);
  border-radius: 4px;
  background: rgba(0, 191, 255, 0.1);
  color: #00bfff;
  font-size: 0.9rem;
  font-family: 'Oswald', sans-serif;
}

.email-input::placeholder {
  color: rgba(0, 191, 255, 0.7);
}

.email-input:focus {
  outline: none;
  border-color: #00bfff;
  box-shadow: 0 0 15px rgba(0, 191, 255, 0.3);
}

.subscribe-btn {
  background: #00bfff;
  color: #000;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  border-radius: 4px;
  box-shadow: 0 0 20px rgba(0, 191, 255, 0.3);
  font-family: 'Oswald', sans-serif;
}

.subscribe-btn:hover:not(:disabled) {
  background: #0099cc;
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(0, 191, 255, 0.5);
}

.subscribe-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.newsletter-error {
  background-color: rgba(255, 68, 68, 0.1);
  border: 1px solid rgba(255, 68, 68, 0.3);
  color: #ff6b6b;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  margin-bottom: 1rem;
  font-size: 0.85rem;
  font-family: 'Inter', sans-serif;
}

.newsletter-success {
  background-color: rgba(0, 191, 255, 0.1);
  border: 1px solid rgba(0, 191, 255, 0.3);
  color: #00bfff;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  margin-bottom: 1rem;
  font-size: 0.85rem;
  font-family: 'Inter', sans-serif;
}

/* ===== ALBUM IMAGES ===== */
.newsletter-album {
  display: flex;
  flex-direction: column;
  gap: 0;
  position: relative;
  width: 160px;
  height: 200px;
}

.newsletter-album-left {
  animation: floatLeft 3s ease-in-out infinite;
}

.newsletter-album-right {
  animation: floatRight 3s ease-in-out infinite;
}

@keyframes floatLeft {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
}

@keyframes floatRight {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
}

.album-image {
  width: 120px;
  height: 160px;
  border-radius: 10px;
  overflow: hidden;
  background: linear-gradient(135deg, #1a1a1a 0%, #0d0d0d 100%);
  border: 2px solid rgba(0, 191, 255, 0.4);
  box-shadow: 0 4px 15px rgba(0, 191, 255, 0.15), 0 8px 20px rgba(0, 0, 0, 0.3);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: absolute;
}

.album-top-left {
  transform: rotate(-8deg);
  top: 0;
  left: 0;
  z-index: 2;
}

.album-bottom-left {
  transform: rotate(6deg);
  top: 70px;
  left: 35px;
  z-index: 1;
}

.album-top-right {
  transform: rotate(8deg);
  top: 0;
  right: 0;
  z-index: 2;
}

.album-bottom-right {
  transform: rotate(-6deg);
  top: 70px;
  right: 35px;
  z-index: 1;
}

.album-image:hover {
  transform: rotate(0deg) scale(1.08);
  border-color: #00bfff;
  box-shadow: 0 0 30px rgba(0, 191, 255, 0.4), 0 12px 25px rgba(0, 0, 0, 0.5);
  z-index: 10;
}

.album-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.album-image:hover .album-img {
  transform: scale(1.1);
}

/* Responsive - Desktop (1200px+) */
@media (min-width: 1200px) {
  .newsletter-album {
    width: 180px;
    height: 230px;
  }

  .album-image {
    width: 135px;
    height: 180px;
    border-radius: 12px;
  }

  .album-bottom-left {
    top: 85px;
    left: 40px;
  }

  .album-bottom-right {
    top: 85px;
    right: 40px;
  }
}

/* Responsive - Large Tablet (1024px - 1199px) */
@media (max-width: 1199px) and (min-width: 1024px) {
  .newsletter-content {
    gap: 2.5rem;
  }

  .newsletter-album {
    width: 155px;
    height: 210px;
  }

  .album-image {
    width: 110px;
    height: 150px;
    border-radius: 10px;
  }

  .album-bottom-left {
    top: 65px;
    left: 30px;
  }

  .album-bottom-right {
    top: 65px;
    right: 30px;
  }
}

/* Responsive - Tablet (768px - 1023px) */
@media (max-width: 1023px) and (min-width: 768px) {
  .newsletter-content {
    flex-direction: column;
    gap: 2rem;
    padding: 2.5rem 1.5rem 0 1.5rem;
  }

  .newsletter-album {
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
    width: auto;
    height: auto;
  }

  .album-image {
    width: 100px;
    height: 135px;
    position: relative;
    top: auto !important;
    left: auto !important;
    right: auto !important;
    z-index: 1 !important;
    border-radius: 9px;
  }

  .album-top-left,
  .album-bottom-left,
  .album-top-right,
  .album-bottom-right {
    transform: rotate(0deg) !important;
    position: relative !important;
    top: auto !important;
    left: auto !important;
    right: auto !important;
  }
}

/* Responsive - Small Mobile (481px - 767px) */
@media (max-width: 767px) and (min-width: 481px) {
  .newsletter-content {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0;
    padding: 2rem 0.5rem 0 0.5rem;
    position: relative;
  }

  .newsletter-album {
    flex-direction: column;
    gap: 0;
    width: 55px;
    height: 95px;
    position: relative;
  }

  .newsletter-album-left {
    order: 1;
    margin-right: 0.5rem;
  }

  .newsletter-album-right {
    order: 3;
    margin-left: 0.5rem;
  }

  .newsletter-text {
    order: 2;
    flex: 1;
    min-width: 0;
    max-width: 280px;
    padding: 0 0.5rem;
  }

  .album-image {
    width: 45px;
    height: 60px;
    position: absolute;
    border-radius: 5px;
    border-width: 1.5px;
  }

  .album-top-left {
    transform: rotate(-10deg);
    top: 0;
    left: 0;
    z-index: 2;
  }

  .album-bottom-left {
    transform: rotate(8deg);
    top: 35px;
    left: 8px;
    z-index: 1;
  }

  .album-top-right {
    transform: rotate(10deg);
    top: 0;
    right: 0;
    z-index: 2;
  }

  .album-bottom-right {
    transform: rotate(-8deg);
    top: 35px;
    right: 8px;
    z-index: 1;
  }
  
  .newsletter-title {
    font-size: 1.15rem;
  }
  
  .newsletter-subtitle {
    font-size: 0.75rem;
  }
  
  .newsletter-description {
    font-size: 0.7rem;
    margin-bottom: 1rem;
  }

  .newsletter-form {
    flex-direction: column;
    gap: 0.4rem;
  }

  .email-input {
    min-width: auto;
    width: 100%;
    padding: 0.1rem 0.4rem !important;
    font-size: 0.6rem !important;
    border-radius: 3px;
    height: 28px !important;
    min-height: 28px !important;
    max-height: 28px !important;
    line-height: 1.2 !important;
    box-sizing: border-box !important;
  }

  .subscribe-btn {
    width: 100%;
    padding: 0.1rem 0.5rem !important;
    font-size: 0.6rem !important;
    border-radius: 3px;
    height: 28px !important;
    min-height: 28px !important;
    max-height: 28px !important;
    line-height: 1.2 !important;
    box-sizing: border-box !important;
  }
}

/* Responsive - Extra Small Mobile (< 481px) */
@media (max-width: 480px) {
  .newsletter-content {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0;
    padding: 1.5rem 0.25rem 0 0.25rem;
    position: relative;
  }

  .newsletter-album {
    flex-direction: column;
    gap: 0;
    width: 50px;
    height: 85px;
    position: relative;
  }

  .newsletter-album-left {
    order: 1;
    margin-right: 0.25rem;
  }

  .newsletter-album-right {
    order: 3;
    margin-left: 0.25rem;
  }

  .newsletter-text {
    order: 2;
    flex: 1;
    min-width: 0;
    max-width: 220px;
    padding: 0 0.35rem;
  }

  .album-image {
    width: 40px;
    height: 55px;
    position: absolute;
    border-radius: 4px;
    border-width: 1px;
  }

  .album-top-left {
    transform: rotate(-12deg);
    top: 0;
    left: 0;
    z-index: 2;
  }

  .album-bottom-left {
    transform: rotate(9deg);
    top: 30px;
    left: 6px;
    z-index: 1;
  }

  .album-top-right {
    transform: rotate(12deg);
    top: 0;
    right: 0;
    z-index: 2;
  }

  .album-bottom-right {
    transform: rotate(-9deg);
    top: 30px;
    right: 6px;
    z-index: 1;
  }

  .newsletter-title {
    font-size: 0.95rem;
    margin-bottom: 0.4rem;
    letter-spacing: 1px;
  }

  .newsletter-subtitle {
    font-size: 0.68rem;
    margin-bottom: 0.4rem;
  }

  .newsletter-description {
    font-size: 0.65rem;
    margin-bottom: 0.85rem;
    line-height: 1.4;
  }

  .newsletter-form {
    flex-direction: column;
    gap: 0.35rem;
  }

  .email-input {
    min-width: auto;
    width: 100%;
    padding: 0.08rem 0.35rem !important;
    font-size: 0.55rem !important;
    border-radius: 3px;
    height: 26px !important;
    min-height: 26px !important;
    max-height: 26px !important;
    line-height: 1.2 !important;
    box-sizing: border-box !important;
  }

  .subscribe-btn {
    width: 100%;
    padding: 0.08rem 0.45rem !important;
    font-size: 0.55rem !important;
    border-radius: 3px;
    height: 26px !important;
    min-height: 26px !important;
    max-height: 26px !important;
    line-height: 1.2 !important;
    box-sizing: border-box !important;
  }
}

/* ===== SPECIFIC MOBILE RESPONSIVE - 1 ROW LAYOUT ===== */
