  border: 1px solid rgba(99, 179, 237, 0.3);
  color: #ffffff;
  border-radius: 4px;
  padding: 6px 12px;
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.85rem;
  align-self: flex-start;
}

.cdfm-back-address-btn:hover {
  background: rgba(99, 179, 237, 0.2);
  border-color: #63b3ed;
}

.cdfm-address-form-fields {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
  margin-bottom: 16px;
}

.cdfm-form-actions {
  margin-top: 16px;
}

.cdfm-save-address-btn {
  background: #3182ce;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 12px 24px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  width: 100%;
}

.cdfm-save-address-btn:hover {
  background: #2c5282;
  transform: translateY(-1px);
}

.cdfm-no-address-section {
  margin-top: 16px;
  padding: 24px;
  text-align: center;
  background: rgba(26, 32, 44, 0.6);
  border: 1px dashed rgba(99, 179, 237, 0.3);
  border-radius: 8px;
}

.cdfm-no-address-content p {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 16px;
}

.cdfm-addresses-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 16px;
}

.cdfm-address-item {
  background: rgba(26, 32, 44, 0.6);
  border: 1px solid rgba(99, 179, 237, 0.2);
  border-radius: 8px;
  padding: 16px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.cdfm-address-item:hover {
  background: rgba(26, 32, 44, 0.8);
  border-color: rgba(99, 179, 237, 0.4);
}

.cdfm-address-item.selected {
  border-color: #63b3ed;
  background: rgba(99, 179, 237, 0.2);
}

.cdfm-address-info {
  flex: 1;
}

.cdfm-address-name {
  color: #ffffff;
  font-size: 0.95rem;
  font-weight: 600;
  margin-bottom: 4px;
}

.cdfm-address-details {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.85rem;
  margin-bottom: 4px;
}

.cdfm-address-phone {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.8rem;
}

.cdfm-address-actions {
  display: flex;
  gap: 8px;
}

.cdfm-edit-address-btn,
.cdfm-delete-address-btn {
  background: transparent;
  border: 1px solid #cbd5e0;
  color: #4a5568;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.cdfm-edit-address-btn:hover {
  background: rgba(49, 130, 206, 0.1);
  border-color: #3182ce;
  color: #3182ce;
}

.cdfm-delete-address-btn:hover {
  background: rgba(229, 62, 62, 0.1);
  border-color: #e53e3e;
  color: #e53e3e;
}

.cdfm-add-address-btn {
  background: #3182ce;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.cdfm-add-address-btn:hover {
  background: #2c5282;
  transform: translateY(-1px);
}

/* Section Header Styles (CheckoutModal-style) */
.cdfm-section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  background: rgba(26, 32, 44, 0.6) !important;
  padding: 16px 20px;
  border-radius: 8px;
  border: 1px solid rgba(99, 179, 237, 0.2);
}

.cdfm-section-header-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.cdfm-section-icon {
  color: #63b3ed;
  font-size: 1.25rem;
}

.cdfm-section-title {
  color: #ffffff !important;
  font-size: 1rem !important;
  font-weight: 700;
  margin: 0;
  letter-spacing: -0.25px;
  text-transform: uppercase;
}

.cdfm-add-address-header-btn {
  background: transparent;
  color: #63b3ed;
  border: 1.5px solid #63b3ed;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.cdfm-add-address-header-btn:hover {
  background: linear-gradient(135deg, #3182ce 0%, #2c5282 100%);
  color: #ffffff;
  border-color: #63b3ed;
  transform: translateY(-1px);
}

/* Form Group Styles */
.cdfm-form-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
  min-width: 0;
  width: 100%;
}

.cdfm-form-group input {
  background: rgba(255, 255, 255, 0.1);
  border: 1.5px solid rgba(99, 179, 237, 0.3);
  border-radius: 8px;
  padding: 10px 14px;
  font-size: 0.9375rem;
  color: #ffffff;
  font-family: 'Inter', sans-serif;
  transition: all 0.2s ease;
  outline: none;
  height: 44px;
  box-sizing: border-box;
}

.cdfm-form-group input:focus {
  border-color: #63b3ed;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 0 3px rgba(99, 179, 237, 0.2);
}

.cdfm-form-group input::placeholder {
  color: rgba(255, 255, 255, 0.5);
  opacity: 0.85;
}

.cdfm-form-group input.error {
  border-color: #e53e3e;
}

.cdfm-error-message {
  color: #e53e3e;
  font-size: 0.8125rem;
  font-weight: 500;
  margin: 0;
}

/* Address Card Styles (CheckoutModal-style) */
.cdfm-address-card {
  background: rgba(26, 32, 44, 0.6);
  border: 2px solid rgba(99, 179, 237, 0.2);
  border-radius: 12px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.2s ease;
  animation: cdfm-slideUp 0.3s ease-out;
}

.cdfm-address-card:hover {
  border-color: rgba(99, 179, 237, 0.5);
  background: rgba(26, 32, 44, 0.8);
  transform: translateY(-2px);
}

.cdfm-address-card.selected {
  background: rgba(99, 179, 237, 0.2);
  border-color: #63b3ed;
}

.cdfm-address-card-content {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.cdfm-address-header {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.cdfm-location-icon {
  font-size: 20px;
  color: #ed8936;
  flex-shrink: 0;
  margin-top: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cdfm-receiver-info {
  flex: 1;
  min-width: 0;
}

.cdfm-receiver-name {
  font-size: 1rem;
  font-weight: 600;
  color: #ffffff;
  margin: 0 0 4px 0;
}

.cdfm-receiver-phone {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

.cdfm-address-details {
  padding-left: 32px;
}

.cdfm-address-line {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.5;
  margin: 0;
}

/* No Address Section Styles */
.cdfm-no-address-icon {
  font-size: 3rem;
  opacity: 0.5;
  color: #ed8936;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cdfm-no-address-text h4 {
  color: #ffffff;
  font-size: 1.125rem;
  font-weight: 600;
  margin: 0 0 4px 0;
}

.cdfm-no-address-text p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.875rem;
  margin: 0;
}