  margin-left: auto;
  margin-right: auto;
  animation: searchHeaderSlideIn 0.3s ease-out;
  gap: 20px;
}

@keyframes searchHeaderSlideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
