/* ============================================
   IMAGE ZOOM MODAL
   ============================================ */

.image-zoom-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  padding: 20px;
  animation: zoomFadeIn 0.2s ease-out;
}

@keyframes zoomFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.image-zoom-container {
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  animation: zoomIn 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes zoomIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.image-zoom-close {
  position: absolute;
  top: -30px;
  right: -70px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 1.2rem;
  z-index: 10001;
}

.image-zoom-close:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.1);
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.3);
}

.image-zoom-img {
  max-width: 90vw;
  max-height: 80vh;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  cursor: zoom-out;
}

.image-zoom-caption {
  margin-top: 16px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  font-family: 'Inter', sans-serif;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.8);
  max-width: 600px;
  padding: 0 20px;
}

.sportswear-product-image {
  cursor: zoom-in !important;
}

.sportswear-product-image:hover {
  filter: brightness(1.1);
}

@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .sportswear-products-grid {
    grid-template-columns: repeat(4, minmax(0, 1fr)) !important;
    gap: 1rem !important;
  }

  .sportswear-product-card {
    height: auto !important;
    min-height: 0 !important;
    max-height: none !important;
  }
}

@media only screen and (max-width: 320px) {
  .sportswear-products-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr)) !important;
    gap: 0.3rem !important;
  }

  .sportswear-product-card {
    width: 100% !important;
    min-width: 0 !important;
    max-width: none !important;
  }
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .image-zoom-close {
    top: 10px;
    right: 10px;
    width: 36px;
    height: 36px;
    font-size: 1rem;
  }

  .image-zoom-img {
    max-width: 95vw;
    max-height: 75vh;
  }

  .image-zoom-caption {
    font-size: 14px;
    margin-top: 12px;
  }
}
